.button-yellow {
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 30px;
  color: #1D2E5B;
  background-color: #FFD74B;
  text-align: center;
  text-transform: uppercase;
  border-radius: 25px;
  display: block;
  width: auto;
  padding: 10px 20px;
}
.button-yellow:hover,
.button-yellow:focus {
  background-color: #FFBD30;
}
.button-yellow:active {
  color: rgba(25, 33, 68, 0.3);
}
.button-yellow--login-nav {
  color: #161C35;
}
.button-yellow--login-nav:active {
  color: rgba(22, 28, 53, 0.3);
}
.button-yellow--login {
  line-height: 20px;
  padding: 10px 40px;
}
.button-yellow--login:active {
  color: rgba(22, 28, 53, 0.3);
}
.button-yellow--submit {
  border: none;
  min-width: 220px;
}
.button-yellow--call {
  line-height: 17px;
  color: #161C35;
  padding: 14px 20px;
}
@media (min-width: 768px) {
  .button-yellow--call {
    font-size: 20px;
    line-height: 20px;
    padding: 15px 20px;
    height: 20px;
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 3;
    grid-row-end: 4;
  }
}
