    display: flex;
    justify-content: space-between;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__toggle {
  position: absolute;
  top: 10px;
  right: 0;
  z-index: 2;
  display: block;
  width: 50px;
  height: 50px;
  background-color: inherit;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    top: 40px;
    right: auto;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff;
  border-radius: 1.5px;
}
.main-nav--opened .main-nav__toggle {
  top: 10px;
  right: 0;
}
@media (min-width: 768px) {
  .main-nav--opened .main-nav__toggle {
    top: 40px;
    right: auto;
    left: 50%;
    transform: translateX(-50%);
  }
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 14px;
  width: 24px;
  height: 2px;
  background-color: #CCCCCC;
  border-radius: 1.5px;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:focus::before,
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:focus::after {
  background-color: #959BB2;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: #000000;
}
@media (max-width: 1439px) {
  .main-nav--closed .site-list {
    display: none;
  }
  .main-nav--closed .users-links--main-nav {
    display: none;
  }
  .main-nav--closed .social-list {
    display: none;
  }
  .main-nav--closed .main-nav__wrapper {
    padding-top: 0;
  }
  .main-nav--opened .site-list {
    display: block;
  }
  .main-nav--opened .users-links--main-nav {
    display: block;
  }
  .main-nav--opened .social-list {
    display: flex;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
}
@media (max-width: 1439px) and (min-width: 768px) {
  .main-nav--closed .users-links--main-nav {
    display: inline;
  }
  .main-nav--closed .users-links__item {
    display: none;
  }
  .main-nav--closed .users-links__item:nth-child(1) {
    display: inline;
  }
}
